import * as THREE from 'three'
import React, { useRef, useState, useEffect } from 'react'
import { Canvas, useFrame } from '@react-three/fiber'
import { Image, Environment, ScrollControls, useScroll, useTexture } from '@react-three/drei'
import { easing } from 'maath'
import { BentPlaneGeometry } from './util'
import './util'

export const App = () => {
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });
  const [selectedCard, setSelectedCard] = useState(null);

  useEffect(() => {
    const handleMouseMove = (event) => {
      setMousePosition({ x: event.clientX, y: event.clientY });
    };

    window.addEventListener('mousemove', handleMouseMove);
    return () => {
      window.removeEventListener('mousemove', handleMouseMove);
    };
  }, []);

  return (
    <div
      style={{
        width: '100vw',
        height: '100vh',
        background: `radial-gradient(circle at ${mousePosition.x}px ${mousePosition.y}px, #ff3a9e, #fad4c4)`,
      }}
    >
      <Canvas camera={{ position: [0, 0, 100], fov: 15 }}>
        <fog attach="fog" args={['#a79', 8.5, 12]} />
        <ScrollControls pages={4} infinite>
          <Rig rotation={[0, 0, 0.15]}>
            <Carousel setSelectedCard={setSelectedCard} />
          </Rig>
          <Banner position={[0, -0.15, 0]} />
        </ScrollControls>
        <Environment preset="dawn" background blur={0.5} />   
      </Canvas>
      {selectedCard && (
        <CardPopup card={selectedCard} onClose={() => setSelectedCard(null)} />
      )}
    </div>
  );
};
// Uncomment this line to add a background reference line 42

function Rig(props) {
  const ref = useRef()
  const scroll = useScroll()
  useFrame((state, delta) => {
    ref.current.rotation.y = -scroll.offset * (Math.PI * 2) // Rotate contents
    state.events.update() // Raycasts every frame rather than on pointer-move
    easing.damp3(state.camera.position, [-state.pointer.x * 2, state.pointer.y + 1.5, 10], 0.3, delta) // Move camera
    state.camera.lookAt(0, 0, 0) // Look at center
  })
  return <group ref={ref} {...props} />
}
 //change the number of cards here
 function Carousel({ radius = 1.4, count = 8, setSelectedCard }) {
  return Array.from({ length: count }, (_, i) => (
    <Card
      key={i}
      index={i}
      url={`/img${Math.floor(i % 10) + 1}_.png`}
      position={[Math.sin((i / count) * Math.PI * 2) * radius, 0, Math.cos((i / count) * Math.PI * 2) * radius]}
      rotation={[0, Math.PI + (i / count) * Math.PI * 2, 0]}
      setSelectedCard={setSelectedCard}
    />
  ))
}

//change functions of the cards here
function Card({ url, index, setSelectedCard, ...props }) {
  const ref = useRef()
  const [hovered, hover] = useState(false)
  const pointerOver = (e) => (e.stopPropagation(), hover(true))
  const pointerOut = () => hover(false)
  const handleClick = (e) => {
    e.stopPropagation()
    setSelectedCard({
      videoUrl: `/video${index + 1}.mp4`, // Assuming you have video files named video1.mp4, video2.mp4, etc.
      index,
      title: `Video ${index + 1}`,
      description: `This is a description for Video ${index + 1}. You can add more details here.`
    })
  }
  useFrame((state, delta) => {
    easing.damp3(ref.current.scale, hovered ? 1.15 : 1, 0.1, delta)
    easing.damp(ref.current.material, 'radius', hovered ? 0.25 : 0.1, 0.2, delta)
    easing.damp(ref.current.material, 'zoom', hovered ? 1 : 1.5, 0.2, delta)
  })
  return (
    <mesh 
      ref={ref} 
      {...props} 
      onPointerOver={pointerOver} 
      onPointerOut={pointerOut}
      onClick={handleClick}
    >
      <bentPlaneGeometry args={[0.1, 1, 1, 20, 20]} />
      <shaderMaterial
        side={THREE.DoubleSide}
        transparent
        uniforms={{
          uTexture: { value: new THREE.TextureLoader().load(url) },
        }}
        vertexShader={`
          varying vec2 vUv;
          void main() {
            vUv = uv;
            gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
          }
        `}
        fragmentShader={`
          uniform sampler2D uTexture;
          varying vec2 vUv;
          void main() {
            vec2 flippedUv = vec2(1.0 - vUv.x, vUv.y);
            gl_FragColor = texture2D(uTexture, flippedUv);
          }
        `}
      />
    </mesh>
  )
}

function CardPopup({ card, onClose }) {
  return (
    <div className="popup-overlay" onClick={onClose}>
      <div className="popup-content" onClick={e => e.stopPropagation()}>
        <button className="close-button" onClick={onClose}>×</button>
        <h2>{card.title}</h2>
        <video 
          controls 
          width="100%" 
          style={{ maxHeight: '60vh' }}
        >
          <source src={card.videoUrl} type="video/mp4" />
          Your browser does not support the video tag.
        </video>
        <p>{card.description}</p>
      </div>
    </div>
  )
}

function Banner(props) {
  const ref = useRef()
  const texture = useTexture('/work_.png')
  texture.wrapS = texture.wrapT = THREE.RepeatWrapping
  const scroll = useScroll()
  useFrame((state, delta) => {
    ref.current.material.time.value += Math.abs(scroll.delta) * 4
    ref.current.material.map.offset.x += delta / 2
  })
  return (
    <mesh ref={ref} {...props}>
      <cylinderGeometry args={[1.6, 1.6, 0.14, 128, 16, true]} />
      <meshSineMaterial map={texture} map-anisotropy={16} map-repeat={[30, 1]} side={THREE.DoubleSide} toneMapped={false} />
    </mesh>
  )
}


